'use client';

import { useEffect, useState } from 'react';
import Navbar from '../components/Navbar';

interface Product {
  id: number;
  name: string;
  description: string;
  price: number;
  stock: number;
}

export default function ProductsPage() {
  const [products, setProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    fetch(`${process.env.NEXT_PUBLIC_API_URL}/products`)
      .then(res => res.json())
      .then(data => setProducts(data))
      .catch(err => console.error('API bağlantı hatası:', err))
      .finally(() => setLoading(false));
  }, []);

  return (
    <div>
      <Navbar />  {/* Navbar buraya ekleniyor */}

      <main className="p-8">
        <h1 className="text-3xl font-bold mb-4">Ürün Listesi</h1>

        {loading ? (
          <p>Yükleniyor...</p>
        ) : products.length === 0 ? (
          <p>Henüz ürün yok.</p>
        ) : (
          <ul className="space-y-2">
            {products.map(p => (
              <li key={p.id} className="border p-4 rounded-lg shadow-sm">
                <h2 className="text-xl font-semibold">{p.name}</h2>
                <p>{p.description}</p>
                <p className="text-sm text-gray-600">
                  Fiyat: {p.price} ₺ | Stok: {p.stock}
                </p>
              </li>
            ))}
          </ul>
        )}
      </main>
    </div>
  );
}
